import { Component, Injector,Optional, OnInit,Inject } from '@angular/core';
import { MatDialogRef, MatCheckboxChange,MAT_DIALOG_DATA} from '@angular/material';
import { finalize } from 'rxjs/operators';
import * as _ from 'lodash';
import { AppComponentBase } from '@shared/app-component-base';
import {
    ResetPwdServiceProxy,
    ResetPwdDto,
  } from '@shared/service-proxies/service-proxies';
  @Component({
    templateUrl: './reset-password-dialog.component.html',
    styles: [
      `
        mat-form-field {
          width: 100%;
        }
        mat-checkbox {
          padding-bottom: 5px;
        }
      `
    ]
})
export class ResetPwdDialogComponent extends AppComponentBase
implements OnInit {
    saving = false;
    reset: ResetPwdDto = new ResetPwdDto(); 
    password: string;     
    constructor(
      injector: Injector,
      public _resetService: ResetPwdServiceProxy,
      private _dialogRef: MatDialogRef<ResetPwdDialogComponent>,
      @Optional() @Inject(MAT_DIALOG_DATA) private _id: number
    ) {
      super(injector);
    }
    
    ngOnInit(): void {
      // this..isTenant=false;     
    }           
    save(): void {
      this.saving = true;      
      this.reset.password=this.password;
      this.reset.userId=this._id;  
      this._resetService
        .resetPwd(this.reset)
        .pipe(
          finalize(() => {
              
            this.saving = false;
          })
        )
        .subscribe((data) => {
            if(data.result==1)
            {
              abp.message.success("VerifySuccessfully")
              this.close(true)
            }
            else
            {
              abp.message.error("Validation Failed, Please Reenter")
            }
          });
        //   this.notify.info(this.l('SavedSuccessfully'));
        //   this.close(true);
        // });
    }
  
    close(result: any): void {
      this._dialogRef.close(result);
    }
  }