import { Component, Injector, OnInit } from '@angular/core';
import { MatDialogRef, MatCheckboxChange } from '@angular/material';
import { finalize } from 'rxjs/operators';
import { MatDialog } from '@angular/material';
import * as _ from 'lodash';
import { AppComponentBase } from '@shared/app-component-base';
import {
    ForgetPwdServiceProxy,
    ForgetDto,
  } from '@shared/service-proxies/service-proxies';
import { ResetPwdDialogComponent } from 'account/reset/reset-password-dialog.component';
import { idLocale } from 'ngx-bootstrap';


@Component({
    templateUrl: './forget-user-dialog.component.html',
    styles: [
      `
        mat-form-field {
          width: 100%;
        }
        mat-checkbox {
          padding-bottom: 5px;
        }
      `
    ]
})
export class ForgetPwdDialogComponent extends AppComponentBase
    implements OnInit {
        saving = false;
        forget: ForgetDto = new ForgetDto(); 
        userName: string;
        emailAddress:string;
        isTenant:boolean;       
        constructor(
          injector: Injector,
          public _forgetService: ForgetPwdServiceProxy,
          private _dialog: MatDialog,
          private _dialogRef: MatDialogRef<ForgetPwdDialogComponent>,
          
        ) {
          super(injector);
        }
      
        ngOnInit(): void {
          this.forget.isTenant=false;     
        }           
        save(): void {
          this.saving = true;      
          this.forget.isTenant = this.isTenant;      
          this._forgetService
            .forgetPwd(this.userName,this.emailAddress,this.isTenant)
            .pipe(
              finalize(() => {
                  
                this.saving = false;
              })
            )
            .subscribe((data) => {
              
                if(data.count==1)
                {
                  abp.message.success("VerifySuccessfully")
                  this.close(true);
                  this._dialog.open(ResetPwdDialogComponent,{data:data.id});
                }
                else
                {
                  abp.message.error("Validation Failed, Please Reenter")
                }
            });
            //   this.notify.info(this.l('SavedSuccessfully'));
            //   this.close(true);
            // });
        }
      
        close(result: any): void {
          this._dialogRef.close(result);
        }
      }
  