import { Component, Injector } from '@angular/core';
import { MatDialog } from '@angular/material';
import { AbpSessionService } from '@abp/session/abp-session.service';
import { AppComponentBase } from '@shared/app-component-base';
import { accountModuleAnimation } from '@shared/animations/routerTransition';
import { LoginService } from './login.service';
import { ForgetPwdDialogComponent } from 'account/forget/forget-user-dialog.component';

@Component({
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.less'],
  animations: [accountModuleAnimation()]
})
export class LoginComponent extends AppComponentBase {
  submitting = false;

  constructor(
    injector: Injector,
    public loginService: LoginService,
    private _dialog: MatDialog,
    private _sessionService: AbpSessionService
  ) {
    super(injector);
  }
   
  get multiTenancySideIsTeanant(): boolean {
    return this._sessionService.tenantId > 0;
  }

  get isSelfRegistrationAllowed(): boolean {
    if (!this._sessionService.tenantId) {
      return false;
    }

    return true;
  }
  forgetPwd(): void {
    this.showForgetPwdDialog();
  }
private showForgetPwdDialog(): void {
 this._dialog.open(ForgetPwdDialogComponent);
}
  login(): void {
    this.submitting = true;
    this.loginService.authenticate(() => (this.submitting = false));
  }
  
}
