import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { JsonpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';

import { ModalModule } from 'ngx-bootstrap';

import { AbpModule } from '@abp/abp.module';

import { AccountRoutingModule } from './account-routing.module';

import { ServiceProxyModule } from '@shared/service-proxies/service-proxy.module';

import { SharedModule } from '@shared/shared.module';

import { AccountComponent } from './account.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { AccountLanguagesComponent } from './layout/account-languages.component';

import { LoginService } from './login/login.service';

// tenants
import { TenantChangeComponent } from './tenant/tenant-change.component';
import { TenantChangeDialogComponent } from './tenant/tenant-change-dialog.component';

import { ForgetPwdDialogComponent } from 'account/forget/forget-user-dialog.component';

//After forget password    reset password
import {ResetPwdDialogComponent} from 'account/reset/reset-password-dialog.component';
import { from } from 'rxjs';
@NgModule({
    imports: [
        CommonModule,
        FormsModule,
        HttpClientModule,
        JsonpModule,
        AbpModule,
        SharedModule,
        ServiceProxyModule,
        AccountRoutingModule,
        ModalModule.forRoot()
    ],
    declarations: [
        AccountComponent,
        LoginComponent,
        RegisterComponent,
        AccountLanguagesComponent,
        // tenant
        TenantChangeComponent,
        TenantChangeDialogComponent,
        ForgetPwdDialogComponent,
        
        //reset password
        ResetPwdDialogComponent
    ],
    providers: [
        LoginService
    ],
    entryComponents: [
        // tenant
        TenantChangeDialogComponent,
        ForgetPwdDialogComponent,

        //reset password
        ResetPwdDialogComponent
    ]
})
export class AccountModule {

}
